.popup-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.6);
  backdrop-filter: blur(8px);
  -webkit-backdrop-filter: blur(8px);
  z-index: 9999;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 1rem;
  opacity: 0;
  transition: opacity 0.3s ease;
  pointer-events: none;
}

.popup-overlay.show {
  opacity: 1;
  pointer-events: auto;
}

.popup-container {
  background-color: rgba(255, 255, 255, 0.95);
  border-radius: 0.75rem;
  box-shadow: 0 25px 50px -12px rgba(0, 0, 0, 0.25);
  width: 100%;
  max-width: 32rem;
  margin: 0 auto;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  max-height: 90vh;
  transform: translateY(20px);
  transition: transform 0.3s ease;
  border: 1px solid rgba(255, 255, 255, 0.18);
}

.popup-overlay.show .popup-container {
  transform: translateY(0);
}

.popup-header {
  padding: 1.25rem 1.5rem;
  border-bottom: 1px solid rgba(229, 231, 235, 0.5);
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: rgba(248, 250, 252, 0.8);
}

.popup-title {
  font-size: 1.25rem;
  font-weight: 600;
  color: #1e293b;
  margin: 0;
}

.popup-close {
  background: none;
  border: none;
  cursor: pointer;
  color: #64748b;
  font-size: 1.5rem;
  padding: 0.25rem;
  border-radius: 50%;
  width: 2rem;
  height: 2rem;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: background-color 0.2s ease;
}

.popup-close:hover {
  background-color: rgba(0, 0, 0, 0.05);
}

.popup-body {
  padding: 1.5rem;
  overflow-y: auto;
  flex-grow: 1;
}

.popup-footer {
  padding: 1rem 1.5rem;
  border-top: 1px solid rgba(229, 231, 235, 0.5);
  background-color: rgba(248, 250, 252, 0.8);
  text-align: right;
}

.popup-btn {
  background-color: #3b82f6;
  color: white;
  padding: 0.5rem 1.25rem;
  border-radius: 0.5rem;
  border: none;
  cursor: pointer;
  font-size: 0.875rem;
  font-weight: 500;
  transition: background-color 0.2s ease;
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
}

.popup-btn:hover {
  background-color: #2563eb;
}

.field-group {
  margin-bottom: 1.25rem;
}

.field-label {
  font-size: 0.875rem;
  font-weight: 500;
  color: #64748b;
  margin-bottom: 0.25rem;
  display: block;
}

.field-value {
  font-size: 1rem;
  color: #0f172a;
  margin-top: 0.25rem;
  line-height: 1.5;
}

.pre-wrap {
  white-space: pre-wrap;
}
